import React from "react";
import Head from "~/components/head/index";
import List from "~/components/list/index";
import { NoticeBar, Carousel, WhiteSpace } from 'antd-mobile';
import { connect } from "react-redux";
import { getMovies } from "~/actions/index";
import "./index.scss";

const mapStateToProps = (state) => {
    return {
        movies: state.getIn(["data", "movies"])
    }
}

class Home extends React.Component {
    componentDidMount() {
        if (!this.props.movies.length) {
            this.props.dispatch(getMovies(
                { url: "/react/movies" }
            ))
        }
    }

    render() {
        const {
            movies
        } = this.props;

        return (
            <div>
                <Head title="首页" show={false} search={true} pop={true} />
                <NoticeBar marqueeProps={{ loop: true, style: { padding: '0 7.5px', } }}>
                    good good study day day up,good good study day day up,good good study day day up,good good study day day up
                </NoticeBar>
                <WhiteSpace />
                <List movies={movies} keys="home" />
            </div>
        )
    }
}

export default connect(mapStateToProps)(Home)