import React from "react";
import { NavBar, Icon, Popover, ActionSheet } from 'antd-mobile';
import PropTypes from "prop-types";
import { history } from "&/history";
import { connect } from "react-redux";
import { clearSearchList } from "~/actions/index";
import "./index.scss";

const mapStateToProps = (state) => {
    return {
        searchList: state.getIn(["data", "searchList"])
    }
}

const Item = Popover.Item;
const myImg = src => <img src={`https://gw.alipayobjects.com/zos/rmsportal/${src}.svg`} className="am-icon am-icon-xs" alt="" />;

class Head extends React.Component {

    constructor() {
        super();
        this.state = {
            visible: false,
            selected: '',
        }
    }

    gotoSearch = () => {
        history.push("/search")
    }

    back = (show, title) => {
        if (show) {
            history.go(-1)
        }

        if (title === "搜索") {
            this.props.dispatch(clearSearchList())
        }
    }

    onSelect = (opt) => {
        this.setState({
            visible: false,
            selected: opt.props.value,
        })
        // console.log(opt.props.value)
        if (opt.props.value == "scan") {
            history.push("/scan")
        } else if (opt.props.value == "login") {
            history.push("/login")
        } else if (opt.props.value == "photo") {
            this.showActionSheet()
        }
    }

    handleVisibleChange = (visible) => {
        this.setState({
            visible,
        })
    }

    showActionSheet = () => {
        const BUTTONS = ["从手机相册选取", "立即拍照"];
        ActionSheet.showActionSheetWithOptions({
            options: BUTTONS,
            cancelButtonIndex: BUTTONS.length - 1,
            destructiveButtonIndex: BUTTONS.length - 2,
            message: '拍照',
            maskClosable: true,
            'data-seed': 'logId',
        },
            (buttonIndex) => {
                this.setState({ clicked: BUTTONS[buttonIndex] });
            });
    }

    render() {
        const {
            title,
            show,
            search,
            pop
        } = this.props;
        return (
            <div>
                <NavBar
                    mode="dark"
                    icon={show && <Icon type="left" />}
                    rightContent={[
                        search && <Icon
                            key="0"
                            type="search"
                            style={{ marginRight: '16px' }}
                            onClick={this.gotoSearch}
                        />,
                        pop && <Popover
                            mask
                            key={1}
                            overlayClassName="fortest"
                            overlayStyle={{ color: 'currentColor' }}
                            visible={this.state.visible}
                            overlay={[
                                (<Item key="4" value="scan" icon={myImg('tOtXhkIWzwotgGSeptou')} data-seed="logId">扫一扫</Item>),
                                (<Item key="5" value="photo" icon={myImg('PKAgAqZWJVNwKsAJSmXd')} style={{ whiteSpace: 'nowrap' }}>拍照</Item>),
                                (<Item key="6" value="login" icon={myImg('uQIYTFeRrjPELImDRrPt')}>
                                    <span style={{ marginRight: 5 }}>登录</span>
                                </Item>),
                            ]}
                            align={{
                                overflow: { adjustY: 0, adjustX: 0 },
                                offset: [-10, 0],
                            }}
                            onVisibleChange={this.handleVisibleChange}
                            onSelect={this.onSelect}
                        >
                            <div style={{
                                height: '100%',
                                padding: '0 15px',
                                marginRight: '-15px',
                                display: 'flex',
                                alignItems: 'center',
                            }}
                            >
                                <Icon type="ellipsis" />
                            </div>
                        </Popover>
                    ]}
                    style={{ backgroundColor: "#d3544d" }}
                    onLeftClick={() => this.back(show, title)}
                >{title}</NavBar>
            </div>
        )
    }
}

Head.propTypes = {
    title: PropTypes.string,
    show: PropTypes.bool,
    search: PropTypes.bool,
    pop: PropTypes.bool
}

export default connect(mapStateToProps)(Head)