import React from "react";
import { history } from "&/history";
import { PullToRefresh } from "antd-mobile";
import { connect } from "react-redux";
import { moviesReverse, searchListReverse } from "~/actions/index";
import "./index.scss";

class List extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            refreshing: false,
            down: true,
            height: document.documentElement.clientHeight,
            data: [],
        };
    }

    gotoDetail = (_id, title) => {
        history.push("/detail/" + _id + "?title=" + title)
    }

    render() {
        const {
            movies,
            keys
        } = this.props;

        return (
            <div className="movie-box">
                <PullToRefresh
                    damping={60}
                    ref={el => this.ptr = el}
                    style={{
                        height: this.state.height,
                        overflow: 'auto',
                    }}
                    indicator={this.state.down ? {} : { deactivate: '上拉可以刷新' }}
                    direction={this.state.down ? 'down' : 'up'}
                    refreshing={this.state.refreshing}
                    onRefresh={() => {
                        this.setState({ refreshing: true });
                        if (keys == "home") {
                            this.props.dispatch(moviesReverse());
                        } else if (keys == "search") {
                            this.props.dispatch(searchListReverse());
                        }
                        setTimeout(() => {
                            this.setState({ refreshing: false });
                        }, 1000);
                    }}
                >
                    {
                        movies && movies.map((item, i) => {
                            return (
                                <dl key={i} className="item-dl">
                                    <dt>
                                        <img src={item.poster} alt="" />
                                    </dt>
                                    <dd>
                                        <ul onClick={() => this.gotoDetail(item._id, item.title)}>
                                            <li className="title-li">{item.title}</li>
                                            <li className="rate-li">观众评价：<span>{item.rate}</span></li>
                                            <li className="name-li">主演:{item.casts.map((cast) => {
                                                cast.name = cast.name + "   ";
                                                return cast.name;
                                            })}</li>
                                            <li className="time-li">时长：{item.duration}</li>
                                        </ul>
                                    </dd>
                                </dl>
                            )
                        })
                    }
                </PullToRefresh>
            </div>
        )
    }
}

export default connect()(List)